.headers {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
}
.logo {
  width: 100;
}

.navitems > ul {
  display: flex;
  font-size: 24px;
}
.navitems > ul > li {
  display: flex;
  padding: 10px;
}

.res-card {
  width: 200px;
  margin: 5px;
  background-color: grey;
  padding: 5px;
}
.res-card:hover {
  border: 1px solid black;
  cursor: pointer;
}
.search {
  padding: 10px;
}
.rest-logo {
  width: 100%;
  height: 50%;
}
.res-container {
  display: flex;
  flex-wrap: wrap;
}

.shimmer {
  height: 400px;
  width: 200px;
  background-color: grey;
  margin: 10px;
  opacity: 0.5;
}
.shimmer-containner {
  display: flex;
  flex-wrap: wrap;
}
